.tableContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 50px;

  .toolbarContainer {
    width: 80%;
    margin-bottom: 20px;
    box-sizing: border-box;

    .toolbar {
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      align-items: center;
      width: 230px;
      background-color: #EFEFEF;
      padding: 15px 0;
      border-radius: 5px;

      .icon {
        margin: 0 10px;
        cursor: pointer;
      }
    }
  }

  .tableBlock {
    width: 80%;
  }
}